body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/bg.jpg") center center no-repeat fixed;
  background-size: cover, cover;
  color: white; }

main {
  flex: 1; }

h1,
h2 {
  font-family: 'Amatic SC'; }

.outer {
  max-width: 1140px; }

footer > p {
  margin: 0; }

h6 {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
  text-transform: uppercase;
  color: #fff;
  opacity: 0; }

a {
  text-decoration: none;
  color: #38fedb; }
  a:visited {
    color: #38fedb; }
  a:hover {
    text-decoration: underline; }

p {
  text-align: justify; }

.text-text {
  margin-bottom: 0; }

.text-indent {
  text-indent: 1.5em; }

.opacity-1 {
  opacity: 1; }

.opacity-0 {
  opacity: 0; }

.hidden {
  display: none !important; }

@keyframes anim-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

h6, .btn-round, .btn-restart, .card, .btn-close, .btn-verdict {
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out, opacity .15s ease-in-out; }

/*
|--------------------------------------------------------------------------
| Control
|--------------------------------------------------------------------------
*/
.round-container {
  text-align: center;
  position: relative;
  background-color: #1b1b1b;
  border: 1em solid #1b1b1b;
  border-bottom: 2em solid #1b1b1b;
  border-top: 2em solid #1b1b1b; }

.btn-round {
  border-radius: 50%;
  width: 90px;
  height: 90px;
  border: 6px #7a1f28 solid;
  background-color: #dc3545;
  color: white;
  text-transform: uppercase; }
  .btn-round:hover {
    background-color: #c82333; }
  .btn-round-num {
    font-size: 2em;
    line-height: 100%; }

.btn-restart {
  position: absolute;
  top: 0;
  right: 0;
  background: url("../img/update-arrows.png");
  width: 32px;
  height: 32px;
  opacity: .6;
  border: none; }
  .btn-restart:hover {
    opacity: 1; }

/*
|--------------------------------------------------------------------------
| Card
|--------------------------------------------------------------------------
*/
.card-containter {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
  grid-gap: 1em;
  justify-items: center;
  background-color: #bfbfbf;
  border: 1em solid #1b1b1b;
  border-top: 0; }

.card {
  display: flex;
  flex-direction: column;
  user-select: none;
  animation: .5s anim-in ease-in-out;
  border: none;
  color: black;
  overflow: hidden; }

.card-control-container {
  padding: 0.4em;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: space-between; }

.btn-close {
  height: 2em;
  width: 2em;
  background-size: 1.2em;
  box-sizing: border-box; }
  .btn-close:focus {
    box-shadow: none; }

.card-content-container {
  background-color: white;
  padding: 1em;
  position: relative; }

.card-container-indent {
  margin-bottom: 1em; }
  .card-container-indent:last-child {
    margin-bottom: 0; }

.card-status-container {
  display: flex;
  width: 100%;
  background: #bfbfbf;
  border-radius: .25rem; }

.card-status:first-child, .btn-verdict:first-child {
  border-top-left-radius: .25rem;
  border-bottom-left-radius: .25rem; }

.card-status:last-child, .btn-verdict:last-child {
  border-top-right-radius: .25rem;
  border-bottom-right-radius: .25rem; }

.card-status:not(:first-child), .btn-verdict:not(:first-child) {
  margin-left: -1; }

.card-status {
  width: 2.375em;
  height: 2.375em;
  line-height: 2.375em;
  color: black;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.125);
  animation: .2s anim-in ease-in-out;
  cursor: pointer; }

.card-status-active {
  position: relative; }
  .card-status-active::before {
    width: 100%;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    border: 18px solid transparent;
    border-top: 10px solid #5b5b5b; }

.btn-status {
  width: 100px;
  color: black;
  background-color: #bfbfbf;
  border: 1px solid rgba(0, 0, 0, 0.125);
  overflow: hidden;
  word-wrap: normal; }
  .btn-status:hover {
    color: #fff;
    background-color: #5b5b5b;
    border-color: #5b5b5b; }
  .btn-check:focus + .btn-status, .btn-status:focus {
    box-shadow: none;
    background-color: #5b5b5b; }
  .btn-check:checked + .btn-status,
  .btn-check:active + .btn-status, .btn-status:active, .btn-status.active {
    color: #fff;
    background-color: #5b5b5b;
    box-shadow: none;
    border-color: #5b5b5b; }
    .btn-check:checked + .btn-status:focus,
    .btn-check:active + .btn-status:focus, .btn-status:active:focus, .btn-status.active:focus {
      box-shadow: none; }

.card-verdict-container {
  display: flex;
  width: 100%; }

.btn-verdict {
  flex-grow: 1;
  position: relative;
  height: 2.375em;
  background: #bfbfbf;
  border: 1px solid rgba(0, 0, 0, 0.125); }
  .btn-verdict:hover {
    background-color: #5b5b5b; }

/* uncss:ignore start */
.btn-overlay-killed:hover::before {
  background-image: url("../img/killed.png"); }

.btn-overlay-killed::before {
  background-image: url("../img/killed-w.png"); }

.btn-overlay-ejected:hover::before {
  background-image: url("../img/ejected.png"); }

.btn-overlay-ejected::before {
  background-image: url("../img/ejected-w.png"); }

.btn-overlay-impostor:hover::before {
  background-image: url("../img/impostor.png"); }

.btn-overlay-impostor::before {
  background-image: url("../img/impostor-w.png"); }

.btn-overlay-crewmate:hover::before {
  background-image: url("../img/crewmate.png"); }

.btn-overlay-crewmate::before {
  background-image: url("../img/crewmate-w.png"); }

.btn-overlay-killed:hover::before, .btn-overlay-killed::before, .btn-overlay-ejected::before, .btn-overlay-impostor::before, .btn-overlay-crewmate::before, .overlay-killed::before, .overlay-ejected::before, .overlay-impostor::before, .overlay-crewmate::before {
  content: "";
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.overlay-killed::before, .overlay-ejected::before, .overlay-impostor::before, .overlay-crewmate::before {
  animation: .2s anim-in ease-in-out;
  z-index: 2; }

.overlay-killed::before {
  background: url("../img/killed.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-ejected::before {
  background: url("../img/ejected.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-impostor::before {
  background: url("../img/impostor.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

.overlay-crewmate::before {
  background: url("../img/crewmate.png") center center no-repeat, rgba(0, 0, 0, 0.65); }

/* uncss:ignore */
